import { ServiceNames } from "../../fe/ServiceNames"
import { fmtBytes } from "../../fe/Units"
import { toA } from "../../fe/toA"
import { cleanup_ } from "../Cleanup"
import { Library } from "../Library"
import { calcVolumesTTL } from "../SetVolumesTTL"
import { suggestedLibraryDirs } from "../SuggestedLibraryDir"
import { libraryMetrics, systemInformation } from "../SystemInfo"
import { LogArgs } from "../cli/LogArgs"
import { libraryHealthCheckSetup } from "../health/LibraryHealthChecks"
import { syncPathsForUI } from "../sync/SyncPaths"
import { InfoArgs } from "./InfoArgs"
import { info } from "./InfoImpl"

async function _run() {
  const cmd = new CLI(
    ServiceNames.info,
    "[FILE...]",
    [
      "* Omit any filenames to run diagnostics and output system configuration, tooling, and health check information.",
      "* File paths should be absolute or relative to cwd.",
      "* Provide 2 file paths to see why PhotoStructure may consider them variants of the same asset.",
      "* To emit valid JSON, set NO_COLOR=1 or use --json."
    ].join("\n")
  )
    .add(InfoArgs, WriteSettingsArg, LogArgs, ColorArgs)
    .parse()

  const opts = cmd.opts()
  const filenames = uniq(cmd.args)
  const flat = isTrue(opts.flat)
  const emitTable = isTrue(opts.table)

  const recountAllTags = isTrue(opts.recountAllTags)
  if (recountAllTags) {
    Settings.recountAllTags.envValue = true
  }
  const cleanup = recountAllTags || isTrue(opts.cleanup)

  if (isTrue(opts.imageHash)) {
    opts.filter ??= []
    opts.filter.push("imageHash")
    opts.filter.push("imageHashComparison")
  }

  const includeFileFilters = isTrue(opts.fileFilters)

  const filters = lazy(() => {
    if (isEmpty(opts.filter)) return
    const arr = toA(opts.filter)
    if (flat && filenames.length <= 1) {
      // don't add nativePath!
    } else {
      arr.unshift("nativePath")
    }
    if (filenames.length === 2) {
      arr.push(...arr.map(ea => "a." + ea), ...arr.map(ea => "b." + ea))
    }
    return arr
  })

  function filter(obj: any): any {
    const f = filters()
    return f == null
      ? obj
      : Array.isArray(obj)
      ? obj.map(ea => pickDeep(ea, ...f))
      : pickDeep(obj, ...f)
  }

  function dump(obj: any) {
    obj = filter(obj)
    if (flat) {
      obj = flattenObject(obj)
    }
    const emitJson = isTrue(opts.json)
    if (flat && isPrimitive(obj)) {
      console.log(obj)
    } else if (emitJson || isFalse(Settings.logColor.value)) {
      console.log(JSON.stringify(obj, undefined, emitJson ? undefined : 2))
    } else if (emitTable) {
      console.table(obj)
    } else {
      console.log(
        util.inspect(obj, {
          depth: 7, // depth 4 will elide tag objects!
          colors: true,
          maxArrayLength: 1024
        })
      )
    }
  }

  setServiceName("info")

  if (isTrue(opts.skipHealth)) {
    HealthCheck.disableAll(true)
  }

  if (notBlank(opts.readSettings)) {
    const result = await importFileSettings_(BaseFile.for(opts.readSettings))
    return dump({
      settings: fromEntries(result.settings.map(ea => [ea.name, ea.value])),
      warnings: result.warnings
    })
  }

  await readSettings()

  if (cleanup) {
    // force ourselves to be a db janitor:
    isDbJanitorService.set(true)
    if (Settings.logLevel.isUnset()) {
      Settings.logStdout.envValue = true
      Settings.logLevel.envValue = "warn"
      setupLogger()
    }
  }

  if (isTrue(opts.versionCheck)) {
    await channelVersionsCache()?.unlink()
    return dump(await checkVersion_())
  }

  if (isTrue(opts.globs) || isTrue(opts.excludeGlobs)) {
    const g = excludeGlobs()
    return dump({
      excludeGlobs: {
        file: g.file.map(ea => pick(ea, "pattern", "desc")),
        dir: g.dir.map(ea => pick(ea, "pattern", "desc"))
      }
    })
  }

  if (isTrue(opts.volumes)) {
    const v = await volumes()
    if (v == null) {
      console.error("--volumes failed. Please retry with --info for details.")
    } else {
      return dump(
        v
          .filter(ea => ea.ignorable !== true)
          .map(vol => ({
            filesystem: vol.filesystem,
            ...vol,
            size: fmtBytes(vol.size),
            used: fmtBytes(vol.used),
            available: fmtBytes(vol.available),
            volsha: volsha(vol.uuid)
          }))
      )
    }
  }

  if (isTrue(opts.mountpoints)) {
    const arr = await mountpoints()
    if (arr == null) {
      console.error(
        "--mountpoints failed. Please retry with --info for details."
      )
    } else {
      return dump(arr)
    }
  }

  if (isTrue(opts.plan) || isTrue(opts.licensing)) {
    const l = (await m())[0]

    const current_license = map(l?.l, ea => ({
      email: ea.sub,
      trial: ea.trial,
      issued_at: ea.iat,
      expires: ea.exp,
      meta: l.meta
    }))
    const plan = await t()

    return dump({
      plan,
      current_license
    })
  }

  if (isTrue(opts.suggestedLibraries)) {
    return dump(await suggestedLibraryDirs())
  }

  if (isTrue(opts.volumesTtl)) {
    return dump({
      volumesTTL: (await calcVolumesTTL()) ?? volumeMetadataTtlMs()
    })
  }

  if (isTrue(opts.childEnv)) {
    return dump(childEnv())
  }

  const l = mkLogger("info")

  if (isTrue(opts.loadLibrary) || isTrue(opts.syncPaths) || cleanup) {
    const ldd = libraryDataDirPosixFile()
    if (ldd == null) {
      return console.error("Error: please set up your library.")
    }
    if (!(await ldd.isDirectory())) {
      return console.error(
        "Error: your library data dir, " + ldd + ", is missing."
      )
    }
    l.info("Waiting for library to spin up...")
    const library = Library.instance()
    try {
      await library?.ready
    } catch (err) {
      l.warn("Failed to spin up library", err)
    }
    const dbFile = pathToDb(ldd, "models")
    if (!(await dbFile.isNonEmptyFile())) {
      return console.error(
        "Error: your library database, " + dbFile + " is missing."
      )
    }
  }

  if (cleanup) {
    await cleanup_()
    return dump({ cleanup: "OK" })
  }

  if (isTrue(opts.syncPaths)) {
    return dump(await syncPathsForUI())
  }

  if (notBlank(opts.isLibrary)) {
    const why = await whyNotLibraryDir(opts.isLibrary)
    return dump(
      compactValues({
        isLibrary: why == null,
        why
      })
    )
  }

  if (isTrue(opts.validate)) {
    if (isEmpty(filenames)) {
      throw new Error("--validate requires filenames")
    }
    Settings.validateVideos.envValue = true
    Settings.validateJpegImages.envValue = true
    Settings.validateRawImages.envValue = true

    return dump(
      await Promise.all(
        filenames.map(async ea => [
          ea,
          notBlankOr(await whyInvalidFile(ea), "OK")
        ])
      )
    )
  }

  if (isEmpty(filenames)) {
    // wait for busyPct to be set. Only necessary for Windows (it doesn't have
    // loadavg!)
    await until(() => CpuUsage.instance().busyPct(), {
      timeoutMs: 2 * secondMs,
      intervalMs: 250
    })
    const obj: any = {
      systemInfo: fromEntries(
        (await systemInformation()).map(({ term, defn }) => [
          asObjectKey(term),
          defn
        ])
      )
    }
    obj.versionCheck = {
      userAgent: await userAgent(),
      ...(await checkVersion_().catch(error => ({ error: errorToS(error) }))),
      currentChannel: currentChannel()
    }
    obj.user_ids = userids()
    obj.group_ids = groupids()
    obj.username = await username()
    obj.paths = {
      libraryDir: Settings.libraryDir.valueOrDefault,
      originalsDir: libraryOriginalsDirPosixFile()?.nativePath,
      systemSettingsFile: systemSettingsFile(),
      librarySettingsFile: librarySettingsFile()?.nativePath,
      libraryPreviewsDir: libraryPreviewsDirPosixFile()?.nativePath,
      librarySyncReportsDir: librarySyncReportsDir()?.nativePath,
      cacheDir: cacheDir(),
      logDir: logDir()
    }

    await libraryHealthCheckSetup()
    obj.healthSummary = await HealthCheck.awaitSettled()
    if (isTrue(opts.health)) {
      obj.healthChecks = await Promise.all(HealthCheck.testResults())
    } else if (isTrue(opts.doctor)) {
      obj.healthChecks = await HealthCheck.notOkResults()
    }

    obj.wrongPsEnvValues = verifyPsEnvSettings()

    if (isTrue(opts.loadLibrary)) {
      obj.libraryMetrics = fromEntries(
        (await libraryMetrics())?.map(ea => [asObjectKey(ea.desc), ea.count]) ??
          []
      )
    }

    obj.volumesTTL = fmtFullDuration(await calcVolumesTTL())

    obj.nonDefaultSettings = fromEntries(
      values(Settings)
        .filter(ea => ea.hasValue())
        .map(ea => [ea.name, ea.humanValue])
    )
    return dump(obj)
  } else if (filenames.length === 2) {
    // If we're comparing 2 files, shove the filters into a and b:
    const f1 = PosixFile.for(filenames[0])
    const f2 = PosixFile.for(filenames[1])
    const af1 = (await mkAssetFile_(f1))!
    const af2 = (await mkAssetFile_(f2))!

    const errors = []

    if (af1 == null) errors.push("Failed to read " + f1)
    if (af2 == null) errors.push("Failed to read " + f2)

    if (isNotEmpty(errors)) {
      return dump({ errors })
    }
    const wns = whyNotSimilarAssetFile(af1, af2)

    const fileComparison =
      wns == null
        ? "These two files will be aggregated into a single asset."
        : "These files represent different assets: " + wns

    let primary: Maybe<string>
    let cmpArr: any
    if (wns == null) {
      const sorted = sortAssetFiles([af1!, af2!])
      primary = sorted[0]?.nativePath
      cmpArr = zipPojos(
        ...(await Promise.all(sorted.map(assetFileSortCriteriaPojo)))
      )
    }
    const a = await info(f1, includeFileFilters)
    const b = await info(f2, includeFileFilters)
    const result: any = {
      fileComparison,
      primary,
      a,
      b,
      cmpArr
    }
    if (Settings.useImageHashes.valueOrDefault) {
      result.imageHashComparison = omit(
        compareImageHashes(af1 as any, af2 as any),
        "a",
        "b"
      )
    }
    return dump(result)
  } else {
    const arr = await mapAsync({
      name: "info",
      arr: filenames,
      f: ea => info(ea, includeFileFilters)
    })
    const result = fromEntries(arr?.map((ea, i) => [i as any, ea]))
    if (arr.length > 1) {
      // run clustering on all the files:
      const assetFiles = compact(
        await mapAsync({
          name: "mkAssetFile",
          arr: filenames,
          f: ea =>
            mkAssetFile_(PosixFile.for(ea)).catch(error => {
              console.error("Failed to read " + ea, error)
              return
            })
        })
      )

      const clusters = await aggregateAssetFiles(assetFiles)
      result.clusters = clusters.map(arr2 => arr2.map(ea => ea.uri))
    }
    return dump(result)
  }
}

export async function run() {
  try {
    await _run()
  } catch (cause) {
    onError(toWrappedError("Failed", { cause, fatal: true }))
  } finally {
    await exit({ reason: "done", status: 0 })
  }
}
