import { sortedKeys } from "../../core/Object"
import { mkAssetFile_ } from "../../core/asset-file/AssetFileFactory"
import { assetFileSortCriteriaPojo } from "../../core/asset-file/AssetFileSorter"
import { omitModeData } from "../../core/color/ModeData"
import { errorToS } from "../../core/error/Error"
import {
  notExcludedDirPredicates,
  whyExcludedDirectoryRecursive
} from "../../core/fs/Excluded"
import { PosixFile } from "../../core/fs/PosixFile"
import { whyRejectFile } from "../../core/fs/PosixFileFilters"
import { toNativePath_ } from "../../core/fs/SimpleFile"
import { whyInvalidFile } from "../../core/img/ValidFile"
import { needsTranscoding } from "../../core/img/Video"
import { Predicates } from "../../core/predicates/Predicates"
import { pathToLibraryAsset } from "../../core/settings/PathToLibraryAsset"
import { CapturedAt } from "../../core/tags/CapturedAt"
import { ExifTags } from "../../core/tags/ExifTags"
import { readTags } from "../../core/tags/ExifTool"
import { notBlankOr } from "../../fe/Blank"
import { localToDate } from "../../fe/Date"
import { DatedJson, Json } from "../../fe/JSON"
import { compactValues, pick } from "../../fe/Object"
import { thenCollect } from "../../fe/Promise"
import { Library } from "../Library"
import { libraryFileFiltersFor } from "../LibraryFileFilters"
import { tagAsset } from "../curators/AssetTagger"
import { AssetFile } from "../model/AssetFile"
import { modelDb } from "../model/ModelDb"
import { AssetFileFinder } from "../sync-file/AssetFileFinder"

export async function info(
  filename: string | PosixFile,
  includeFileFilters: boolean
) {
  try {
    const file = PosixFile.for(filename)

    // const why = await whyRejectFile(file)
    // if (why != null) {
    //   return { nativePath: toNativePath_(filename), rejected: why }
    // }

    const dir = file.parent()

    const af = await mkAssetFile_(file)

    const dirFilters = includeFileFilters
      ? await Predicates.explain(dir, ...notExcludedDirPredicates(dir))
      : undefined

    const fileFilters = includeFileFilters
      ? await Predicates.explain(
          file,
          ...libraryFileFiltersFor(file, { validateFile: true })
        )
      : undefined

    const dominantColors = af.dominantColors
      ?.filter(ea => ea?.color?.name != null)
      .map(ea => ({
        rgb: ea.rgbhex,
        name: ea.color?.name,
        pct: ea.pct
      }))

    const tagResult = (await tagAsset({
      primaryVariation: file,
      files: [],
      priorTagPaths: [],
      capturedAts: [],
      uris: [(await file.uri_())!]
    })) as any as Json

    const priorAssetFile =
      modelDb() == null ? undefined : await new AssetFileFinder(file).prior()
    const priorIsInSync = await priorAssetFile?.matchesFile()
    const pathsInLibrary = await thenCollect(
      AssetFile.sameShaInLibrary(file),
      ea => ea.getNativePath()
    )

    const t = await readTags(file)

    const whyRejected = whyRejectFile(
      file,
      // If we can, we should add the library filters (which will be used by AssetFileImporter):
      Library.instance()?.isReadySync() === true
        ? libraryFileFiltersFor(file)
        : // these are all the filters, minus the sha blocklist:
          undefined
    )

    // Assert NativeValued to prevent any values from being promises:
    const result = compactValues({
      ...omitModeData(af),
      ...pick(t!, "tz", "tzSource", "errors", "inferred", "duration"),
      dirFilters,
      dominantColors,
      fileFilters,
      needsTranscoding: await needsTranscoding(file),
      pathsInLibrary,
      pathToLibraryAsset: pathToLibraryAsset(
        localToDate(af.capturedAtLocal),
        file
      ),
      priorAssetFile: priorAssetFile?.toJSON(),
      priorIsInSync,
      sidecars: (await file.existingSidecars()).map(ea => ea.base),
      tagResult,
      validFile: notBlankOr(await whyInvalidFile(file.nativePath), "OK"),
      variantSortCriteria: assetFileSortCriteriaPojo(af),
      whyExcludedDirectoryRecursive: await whyExcludedDirectoryRecursive(dir),
      whyRejected
    }) satisfies
      | DatedJson
      | Record<string, CapturedAt | typeof dominantColors>
      | Pick<ExifTags, "tz" | "tzSource" | "errors" | "inferred">
    return compactValues(sortedKeys(result))
  } catch (error) {
    return {
      nativePath: toNativePath_(filename),
      error: errorToS(error)
    }
  }
}
