import { Command } from "commander"
import { CommandPlugin } from "../../core/cli/CLI"

export const InfoArgs: CommandPlugin = {
  beforeParse(cmd: Command): Command {
    return cmd
      .option(
        "--volumes",
        `Emit a table with currently mounted volume metadata. See https://photostructure.com/volumes for details.`
      )
      .option(
        "--mountpoints",
        `Emit a table with currently mountpoints. See https://photostructure.com/volumes for details.`
      )
      .option(
        "--sync-paths",
        `Emit a table listing the paths that will be synced next.`
      )
      .option(
        "--plan, --licensing",
        `Emit plan/licensing information. See the About page in the web UI for more details or to activate a subscription.`
      )
      .option(
        "--child-env",
        `Emit the environment variables provided to all spawned processes.`
      )
      .option(
        "--validate",
        `Only run file validation on the given filenames (requires current PLUS subscription).`
      )
      .option("-j, --json", "Emit metadata as un-indented, uncolorized JSON.")
      .option(
        "--load-library",
        "Load the library database so more metadata can be shown (like if a file is currently in your library). This adds a second or two (depending on the size of your library)."
      )
      .option(
        "--filter <term>",
        `Case-insensitively filter keys by <term>. <term> may have dots. Note that .nativePath is always included. Repeat to filter for multiple terms ("--grep foo --grep bar").`,
        (val, prev) => prev.concat([val]),
        [] as string[]
      )
      .option(
        "--file-filters",
        `Include an exhaustive list of file and directory filters. This is hundreds of lines long, so it's not included by default.`
      )
      .option(
        "--image-hash",
        `Automatically filter image hash fields. Equivalent to "--filter imageHash --filter imageHashComparison"`
      )
      .option("--flat", "Only return values for objects with only one field")
      .option(
        "--suggested-libraries",
        `Emit the suggested library directories (as seen on the settings page).`
      )
      .option(
        "--volumes-ttl",
        `Emit the volume cache time-to-live (use with --debug to see details).`
      )
      .option(
        "--is-library <DIR>",
        `Run a couple quick checks to see if <DIR> looks like a PhotoStructure library.`
      )
      .option(
        "--read-settings <FILE>",
        `Read the given file and output the parsed settings.`
      )
      .option(
        "--recount-all-tags",
        `Recount every asset tag count. This may take a minute or two. Don't have a sync running concurrently. Implies --cleanup.`
      )
      .option(
        "--globs, --exclude-globs",
        `Emit all file and directory exclusion globs.`
      )
      .option("--health", `Emit details for all health checks.`)
      .option("--doctor", `Emit details for all health checks that aren't OK.`)
      .option("--skip-health", `Disable health checks.`)
      .option(
        "--version-check",
        `Check now if the current version is the latest.`
      )
      .option(
        "--table",
        `Emit with console.table() (useful with --volumes or --filter).`
      )
      .option("--cleanup", `Run a bunch of maintenance tasks.`)
  },
  afterParse: function () {
    // handled by InfoCLI
  }
}
